#root, html{
  background-color: hsl(0, 67%, 5%);
}
.App {
  background-color: #000;
  .Main {
    padding: 0;
    margin: 0;

    .header {
      background-color: black;
      color: #fff;
      padding: 1em;
      text-align: center;

      h1 {
        font-size: 1.5em;
        width: 100%;
      }
    }

    .fa-bars {
      position: fixed;
      top: 10px;
      right: 5px;
      width: 40px;
      font-size: 25px;
      background-color: #777;
      color: #fff;
      text-align: center;
    }

    nav {
      padding: 1em 0;

      ul {
        position: fixed;
        top: 30px;
        background-color: #777;
        width: 100%;
        text-align: center;
        display: none;
        padding: 4em 0em;

        li {
          a {
            color: #fff;

            &:hover {
              color: #777;
            }
          }
        }
      }

      .display {
        display: block;
      }
    }

    .search {
      padding: 0;

      .input-text {
        display: flex;
        justify-content: space-between;

        input[type=text] {
          width: 65%;
        }

        input[type=number] {
          width: 35%;
        }
      }
    }

    .loading {
      width: 100%;
      img{
        width: 50%;
      }
    }

    .movieDisplay {
      margin: 2em 0;
      padding: 1em;
      background-color: #000;
      color: #fff;
      border: 1px solid ;

      p {
        margin: 0 auto;
        font-size: 14px;
      }

      .title {
        font-size: 15px;
        font-weight: bolder;
        margin: 5px 0;
      }

      span {
        font-family: 'Times New Roman', Times, serif;
        text-transform: uppercase;
        font-size: 16;
        font-weight: bolder;
      }

      .plot {
        margin: 5px 0;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-weight: bold;
      }
    }

    .imgPoster {
      border: 2px solid #000;
      width: 100%;
    }


    .moviecard {
      margin: 1em 0;
    }

    .card {
      padding: 3em 2em;
      margin: 1em 0;
      text-align: center;
    }
  }
}

@media(min-width:700px) {
  #root{
    padding-top: 2em;
  }
  html {
    padding: 0;
    margin: 0;
  }

  .App {
    max-width: 70%;
    width: 60%;
    margin: 0 auto;
    position: relative;
    padding: 2em 0;

    .Main {
      width: 100%;
      margin: 0 auto;

      .header {
        width: 100%;
        margin: 0 auto;
        padding-top: 4em;
      }

      .container {
        width: 100%;
        margin: 0 auto;
        padding: 0;
        position: relative;

        .fa-bars {
          display: none;
        }

        nav {
          position: fixed;
          top: 20px;
          width: 60%;
          padding: 0;

          ul {
            width: 100%;
            position: static;
            background-color: #777;
            text-align: center;
            padding: 0;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;

            li {
              width: 100%;

              a {
                width: 100%;
              }
            }
          }

          .display {
            display: block;
          }
        }

        .search {
          width: 100%;
          
          padding: 1em;

          .input-text {
            width: 100%;
            display: flex;
            justify-content: space-between;
            

            input[type=text] {
              width: 70%;
            }

            input[type=number] {
              width: 30%;
            }
          }
        }

        .loading {
          width: 50%;
          max-width: 700px;

        }

        .movieDisplay {
          width: 100%;
          margin: 0.7em 0;
          margin-bottom: 3em;
          padding: 0;
          display: flex;
          flex-wrap: wrap;

          .img-container {
            width: 50%;
            height: 100%;

            .imgPoster {
              border: 2px solid #000;
              width: auto;
              width: 100%;
            }
          }

          .movieInfo-container {
            width: 50%;
            padding: 2em 1em 1em;

            p {
              font-size: 18px;
            }

            .title {
              font-size: 20px;
              font-weight: bolder;
            }

            .plot {
              margin: 5px;
              font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
              font-size: 18px;
            }
          }
        }

        .moviecard {
          margin: 1em 0;
        }

        .card {
          padding: 3em 2em;
          margin: 1em auto;
          text-align: center;
          width: 95%;
        }
      }
    }
  }
}